@import "../helpers/variables";

// Cart Section
.cart {
  min-height: 100vh;
  padding: $navbarHeight 0;
  background-color: #eee;
  .row {
    .column {
      margin: auto;
      // background-color: $backcolor;
      color: #fff;
      margin-top: $navbarHeight;

      > p {
        font-family: $DMFont;
        font-size: 30px;
        padding: 20px;
        margin: 0;
        background-color: $backcolor;
      }
      hr {
        border-color: #eee;
      }
      .products-shopping {
        min-height: 400px;
        > i {
          font-size: 220px;
          color: #25bad0;
          text-align: center;
          padding: 40px 0;
          display: block;
        }
        a {
          margin: auto;
          padding: 7px 30px;
          font-family: $DMFont;
          color: #fff;
          border: 1px solid $lightcolor;
          background-color: $backcolor;
          &:hover {
            border: 1px solid $lightcolor;
            background-color: $lightcolor;
          }
        }
        ul {
          overflow: hidden;
          li {
            .item {
              margin-bottom: 10px;
              background-color: #fff;
              color: #000;
              font-size: 19px;

              .left {
                i {
                  color: $backcolor;
                  float: right;
                  position: relative;
                  right: 10px;
                  top: 7px;
                  cursor: pointer;
                }
              }
              .image {
                display: flex;
                height: 97px;
                justify-content: center;
                align-items: center;
                padding: 0;
              }
              .product-content {
                .content {
                  .title {
                    height: 30px;
                  }
                  .price {
                    height: 30px;
                  }
                  .subtotal {
                    height: 20px;
                    > hr {
                      border-style: dashed;
                      border-color: rgba(0, 0, 0, 0.3);
                      top: -5px;
                      position: relative;
                    }
                  }
                }
              }
            }
          }
          .media {
            width: 100%;
            margin: 20px 0;
            background-color: #fff;
            color: #000;

            img {
              width: 36%;
            }
            .media-body {
              position: relative;
              .glasses-price,
              .lenses-price {
                height: 35px;
                p {
                  // font-family: $DMFont;
                  margin-top: 10px;
                  font-size: 23px;
                  font-size: 19px;
                  display: inline-block;
                  &:last-of-type {
                    right: 70px;
                    position: absolute;
                  }
                }
              }
              i {
                position: absolute;
                top: 7px;
                right: 12px;
                font-size: 20px;
                color: $backcolor;
                cursor: pointer;
              }
            }
          }
        }
      }
      .total-price {
        background-color: #fff;
        padding: 15px 21px;
        font-size: 18px;

        p {
          display: inline-block;
          color: rgba(0, 0, 0, 0.4);
          font-family: $DMFont;
          margin: 0;

          &:last-of-type {
            float: right;
          }
        }
      }

      .accordion {
        .card {
          border: none;
          border-top: 1px solid rgba(0, 0, 0, 0.3);
          .card-header {
            height: 50px;
            padding: 15px 21px;
            background: #fff;
            border: none;
            h2 {
              margin: -12px;
              font-family: $DMFont;
              button {
                text-decoration: none;
                width: 100%;
                text-align: left;
                color: rgba(0, 0, 0, 1);
              }
            }
          }
          .card-body {
            padding: 1rem;
            // coupon
            form {
              input {
                width: 85%;
                border-radius: 0.25rem 0 0 0.25rem;
              }
              button {
                background-color: $lightcolor;
                border-radius: 0 0.25rem 0.25rem 0;
                width: 15%;
                text-align: center;
                i {
                  color: #fff;
                }
              }
            }
          }
        }
      }

      // Modal
      .login-modal {
        background-color: #fff;
        border-top: 1px solid rgba(0, 0, 0, 0.3);
        font-size: 18px;
        padding: 8px 10px;
        button {
          width: 100%;
          text-align: left;
          font-family: $DMFont;
          &:focus {
            box-shadow: none;
          }
        }
        .modal {
          padding-top: 60px;
          .modal-dialog {
            .modal-content {
              background: $backcolor;
              box-shadow: 1px 5px 23px #000;
              color: #fff;
              .modal-header {
                border-color: rgba(255, 255, 255, 0.4);
                display: block;
                position: relative;
                padding-top: 50px;
                padding-bottom: 25px;
                ul {
                  position: absolute;
                  top: 30px;
                  left: 30px;
                  li {
                    display: inline-block;
                    padding-right: 20px;
                    a {
                      text-decoration: none;
                      font-size: 20px;
                      font-family: $DMFont;
                      color: #fff;
                      &::after {
                        content: "";
                        display: block;
                        width: 0;
                        height: 2px;
                        background-color: #25bad0;
                        -webkit-transition: width 0.3s;
                        transition: width 0.3s;
                      }
                      &:hover::after {
                        width: 100%;
                        -webkit-transition: width 0.3s;
                        transition: width 0.3s;
                      }
                    }
                    .active {
                      color: $lightcolor;
                      &::after {
                        content: "";
                        display: block;
                        width: 100%;
                        height: 2px;
                        background-color: #25bad0;
                        -webkit-transition: width 0.3s;
                        transition: width 0.3s;
                      }
                    }
                  }
                }
                .close {
                  color: #fff;
                  font-size: 25px;
                  position: absolute;
                  top: 10px;
                  right: 15px;
                  width: auto;
                  transition: 0.4s;
                  &:not(:disabled):not(.disabled):hover,
                  &:not(:disabled):not(.disabled):focus {
                    color: $lightcolor;
                    text-decoration: none;
                  }
                }
              }
              .modal-login,
              .modal-register {
                padding-top: 0;
                form {
                  .form-group {
                    position: relative;
                    input {
                      background-color: rgba(0, 0, 0, 0.4);
                      border: none;
                      color: #fff;
                      margin-top: 30px;
                      &:focus ~ label {
                        top: -27px;
                        left: 0;
                        color: #25bad0;
                        font-weight: bold;
                      }
                    }
                    label {
                      position: absolute;
                      top: 7px;
                      left: 11px;
                      color: #fff;
                      pointer-events: none;
                      transition: 0.5s;
                    }
                    &:last-of-type {
                      top: 50px;
                      button {
                        width: 30%;
                        margin: auto;
                        text-align: center;
                      }
                    }
                  }
                  .chechbox {
                    position: relative;
                    .form-check-input {
                      left: 24px;
                      margin-top: 1rem;
                      display: block;
                      &:focus ~ label {
                        position: absolute;
                        top: 7px;
                        left: 28px;
                        color: #fff;
                        font-weight: 300;
                      }
                    }
                    label {
                      position: absolute;
                      top: 7px;
                      left: 28px;
                      color: #fff;
                      font-weight: 300;
                    }
                  }
                }
              }
              .modal-login {
                height: 270px;
              }
              .modal-register {
                display: none;
                height: 350px;
              }
            }
          }
        }
      }
    }

    .noItem {
      display: none;

      background-color: $backcolor;
      padding: 0;
      margin: auto;
      color: #fff;
      margin-top: $navbarHeight;
      > p {
        font-family: $DMFont;
        font-size: 30px;
        padding: 20px;
        margin: 0;
        background-color: $backcolor;
      }
      hr {
        border-color: #eee;
      }
      .products-shopping {
        min-height: 400px;
        text-align: center;

        > i {
          font-size: 220px;
          color: #25bad0;
          text-align: center;
          padding: 40px 0;
          display: block;
        }
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .cart
    .row
    .column
    .products-shopping
    ul
    li
    .item
    .product-content
    .content
    .price {
    padding: 0;
  }
  .cart
    .row
    .column
    .products-shopping
    ul
    li
    .item
    .product-content
    .content
    .title {
    padding: 0;
  }
}
