@import "../helpers/variables";

// Nav Section
.navbar {
  background-color: $backcolor;
  height: $navbarHeight;
  z-index: 999999;
  box-shadow: 1px 1px 5px #000;
  .navbar-brand {
    font-family: $brandFont;
    color: $lightcolor;
    font-size: 2.2em;
    padding: 0;
    margin: 0;
  }
  .navbar-toggler {
    margin-top: -13px;
  }

  .navbar-nav {
    .nav-item {
      .nav-link {
        font-size: 19px;
        color: rgba(255, 255, 255, 0.5);
        &::after {
          content: "";
          display: block;
          width: 0;
          height: 2px;
          background-color: $lightcolor;
          transition: width 0.3s;
        }
        &:hover::after {
          width: 100%;
          transition: width 0.3s;
        }
        &:hover {
          color: rgba(255, 255, 255, 1);
        }
      }
      .active {
        color: $lightcolor;
        &::after {
          content: "";
          display: block;
          width: 100%;
          height: 2px;
          background-color: $lightcolor;
          transition: width 0.3s;
        }
        &:hover {
          color: $lightcolor;
        }
      }
    }
  }
  .shopping {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 20px;
    left: 94%;
    a {
      position: relative;
      i {
        font-size: 20px;
        color: $lightcolor;
      }
      span {
        width: 20px;
        height: 20px;
        background-color: $lightcolor;
        position: absolute;
        border-radius: 50%;
        top: -13px;
        right: -13px;
        text-align: center;
        line-height: 20px;
        color: #fff;
        font-size: 13px;
        border: 1px solid;
        border-color: $backcolor;
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .navbar {
    .navbar-toggler {
      margin-right: 46px;
    }
    .shopping {
      left: 88%;
    }

    .navbar-collapse {
      background: $backcolor;
      padding: 0 15px;
    }
  }
}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {
  .navbar {
    .navbar-toggler {
      margin-right: 50px;
    }
    .shopping {
      left: 90%;
    }

    .navbar-collapse {
      background: $backcolor;
      padding: 0 15px;
    }
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px) {
  .navbar-collapse {
    background: $backcolor;
    padding: 0 15px;
  }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {
  .navbar .navbar-collapse {
    background: transparent;
    margin-right: 30px;
  }
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
}
