@import "../helpers/variables";

.view-product {
  padding-top: $navbarHeight;
  position: relative;
  .view-option {
    padding-top: 40px;

    .left {
      text-align: left;
      .product-option {
        float: left;
        width: 45%;
        margin: 0;

        $carousel-breakpoints: (
          sm: 576px - 30px,
          md: 768px - 30px,
          lg: 992px - 30px,
          xl: 1200px - 30px
        );

        .c-carousel {
          align-items: center;
          display: flex;
          justify-content: space-between;
          width: 100%;
          margin: auto;
          .c-carousel__control {
            position: relative;
            &:first-of-type {
              left: -10px;
            }
            &:last-of-type {
              right: -10px;
            }
          }
          .c-carousel__item {
            display: inline-block;
            width: 33%;
            text-align: center;
            padding: 0;
            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              border: 1px solid rgba(0, 0, 0, 0.2);
              padding: 2px;
              background: #fff;
              cursor: pointer;
              transition: 0.3s;
            }
            img.active {
              border: 1px solid rgba(0, 0, 0, 0.8);
            }
          }

          &--disable-prev {
            .c-carousel__control--prev {
              opacity: 0.5;
            }
          }

          &--disable-next {
            .c-carousel__control--next {
              opacity: 0.5;
            }
          }

          @media screen and (min-width: map-get($carousel-breakpoints, "sm")) {
            &--small {
              width: 50%;
            }
          }

          &__control {
            flex: 1 1 40px;
            text-align: center;
            z-index: 2;
          }

          &__track {
            display: flex;
            flex: 1 1 100%;
            flex-wrap: nowrap;
            margin-left: -9px;
            margin-right: -15px;
            overflow: hidden;
            z-index: 1;
          }

          &__item {
            flex: 1 0 auto;
            padding: 0 15px;
            transition: all 0.5s ease-out;
            width: 30%;
          }
        }
      }

      .color-description {
        float: right;
        line-height: 13px;
        width: 55%;
        .color-name {
          display: inline-block;
          font-size: 18px;
          padding: 15px 0;
          color: rgba(0, 0, 0, 0.5);
          font-family: $brandFont;
        }
      }
    }

    .mid {
      text-align: center;
      line-height: 3;

      .measure {
        font-size: 20px;
        font-family: $DMFont;
        cursor: pointer;
        p {
          display: inline-block;
          padding-left: 10px;
        }
      }
    }
    .right {
      text-align: right;
      line-height: 40px;

      div {
        display: inline-block;
      }
      .price {
        font-size: 25px;
        font-family: $DMFont;
        padding-right: 40px;
      }
      .option {
        a {
          margin-right: 20px;
          display: inline-block;
          width: auto;
          font-size: 20px;
          font-family: $DMFont;
          transition: 0.4s;
          &:first-of-type {
            background: #fff;
            color: $backcolor;
            // padding: auto 20px;
            border: 1px solid #000;
            &:hover {
              background: $backcolor;
              color: #fff;
            }
          }
          &:last-of-type {
            border: 1px solid $lightcolor;
            // font-size: 1.3em;
            color: #fff;
            background-color: $lightcolor;
            &:hover {
              background-color: $backcolor;
              box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.5);
            }
          }
        }
      }
    }
  }
  .modal {
    .modal-dialog {
      padding-top: $navbarHeight;
      font-family: $DMFont;
      color: #fff;
      max-width: 50%;
      .modal-content {
        background: $backcolor;
        .modal-header {
          margin: auto;
        }
        .lenses-measure {
          padding-top: 30px;
          .table {
            color: $lightcolor;
            tbody {
              tr {
                td {
                  position: relative;
                  input {
                    position: absolute;
                    top: 0;
                    left: 0;
                    border-radius: 0;
                    width: 100%;
                    height: 100%;
                    border: none;
                    color: #000;
                    text-align: center;
                    padding: 0;

                    &:focus {
                      box-shadow: none;
                    }
                  }
                }
              }
            }
          }
          form {
            .add {
              text-align: right;
              margin-bottom: 0;
              margin-top: 1.5rem;
              .btn {
                padding: 7px 30px;
                font-family: $DMFont;
                border: 1px solid $lightcolor;
                color: #fff;
                background-color: $lightcolor;
                &:hover {
                  border: 1px solid $lightcolor;
                  color: #fff;
                  background-color: $backcolor;
                }
              }
            }
          }
        }

        .lenses-colors {
          display: none;
          padding-top: 30px;
          .fashion-tints,
          .gradient-tints,
          .sunglasses-tints {
            display: table;
            p {
              line-height: 25px;
              display: flex;
              float: left;
              padding: 7px 30px;
            }
            ul {
              display: flex;
              float: left;
              li {
                display: inline-block;
                width: 40px;
                height: 40px;
                background-color: #eee;
                border-radius: 50%;
                margin-right: 5px;
                cursor: pointer;
              }
              .active {
                border: 2px solid #000;
              }
            }
          }
          .fashion-tints {
            li {
              //  lightgray, lightgreen, fuchsia, lavender, orange, rose, and yellow.
              &:nth-of-type(1) {
                background: lightgray;
              }
              &:nth-of-type(2) {
                background: lightgreen;
              }
              &:nth-of-type(3) {
                background: fuchsia;
              }
              &:nth-of-type(4) {
                background: lavender;
              }
              &:nth-of-type(5) {
                background: orange;
              }
              &:nth-of-type(6) {
                background: rosybrown;
              }
            }
          }

          .gradient-tints {
            li {
              // gray, amber, green, blue, pink, and purple.
              &:nth-of-type(1) {
                background-image: linear-gradient(
                  180deg,
                  #808080 0%,
                  #ffffff 100%
                );
              }
              &:nth-of-type(2) {
                background-image: linear-gradient(
                  180deg,
                  #ffbf00 0%,
                  #ffffff 100%
                );
              }
              &:nth-of-type(3) {
                background-image: linear-gradient(
                  180deg,
                  lightgreen 0%,
                  #ffffff 100%
                );
              }
              &:nth-of-type(4) {
                background-image: linear-gradient(
                  180deg,
                  blue 0%,
                  #ffffff 100%
                );
              }
              &:nth-of-type(5) {
                background-image: linear-gradient(
                  180deg,
                  pink 0%,
                  #ffffff 100%
                );
              }
              &:nth-of-type(6) {
                background-image: linear-gradient(
                  180deg,
                  purple 0%,
                  #ffffff 100%
                );
              }
            }
          }
          .sunglasses-tints {
            li {
              // darkgray, darkgreen and darkamber
              &:nth-of-type(1) {
                background-color: #212121;
              }
              &:nth-of-type(2) {
                background-color: #001e00;
              }
              &:nth-of-type(3) {
                background-color: #700e01;
              }
            }
          }
          form {
            .add {
              text-align: right;
              margin-bottom: 0;
              .btn {
                padding: 7px 30px;
                font-family: $DMFont;
                border: 1px solid $lightcolor;
                color: #fff;
                background-color: $lightcolor;
                &:first-of-type {
                  float: left;
                }
                &:last-of-type {
                  float: right;
                }
                &:hover {
                  border: 1px solid $lightcolor;
                  color: #fff;
                  background-color: $backcolor;
                }
              }
            }
          }
        }

        .lenses-glass {
          display: none;
          .table {
            thead {
              tr {
                th {
                  text-transform: uppercase;
                  color: #fff;
                  letter-spacing: 2px;
                  cursor: pointer;
                }
              }
            }
            tbody {
              tr:first-of-type {
                td {
                  img {
                    width: 70px;
                  }
                }
              }
              tr {
                th {
                  text-transform: uppercase;
                  color: #fff;
                  letter-spacing: 2px;
                  line-height: 60px;
                  padding: 0;
                }
                td {
                  padding: 0;

                  ul {
                    position: relative;
                    top: 16px;
                    margin: 0;

                    li {
                      display: inline-block;
                      width: 30px;
                      height: 30px;
                      border-radius: 50%;
                      background-color: #eee;
                    }
                    .select {
                      background-color: $lightcolor;
                    }
                  }
                }
              }
            }
          }
          form {
            .add {
              text-align: right;
              margin-bottom: 0;
              .btn {
                padding: 7px 30px;
                font-family: $DMFont;
                border: 1px solid $lightcolor;
                color: #fff;
                background-color: $lightcolor;
                &:first-of-type {
                  float: left;
                }
                &:last-of-type {
                  float: right;
                }
                &:hover {
                  border: 1px solid $lightcolor;
                  color: #fff;
                  background-color: $backcolor;
                }
              }
            }
          }
        }
      }
    }
  }
  .view-img {
    .images {
      .carousel {
        a {
          i {
            color: #000;
            font-size: 25px;
          }
        }
      }
    }
  }
  .add-to-cart {
    .review {
      text-align: center;

      .name {
        h2 {
          font-size: 36px;
          font-family: $DMFont;
        }
      }
      .stars {
        i {
          font-size: 20px;
          padding-right: 5px;
          color: $starsColor;
        }
      }
    }
    .description {
      p {
        font-size: 18px;
      }
    }
  }
}

@media (max-width: 399px) {
  .view-product .view-option .left .product-option,
  .view-product .view-option .left .color-description {
    width: 100%;
  }
  .color-description {
    margin-top: 10px;
  }
  .view-product .modal .modal-dialog {
    max-width: 100%;
  }
  .modal-body {
    padding: 0.4rem;
  }
  .view-product .table th {
    padding: 0.3rem;
  }

  .view-product
    .modal
    .modal-dialog
    .modal-content
    .lenses-colors
    .fashion-tints,
  .view-product
    .modal
    .modal-dialog
    .modal-content
    .lenses-colors
    .gradient-tints,
  .view-product
    .modal
    .modal-dialog
    .modal-content
    .lenses-colors
    .sunglasses-tints {
    p,
    ul {
      display: block;
      width: 100%;
    }
  }
  .view-product
    .modal
    .modal-dialog
    .modal-content
    .lenses-glass
    .table
    tbody
    tr
    td
    ul
    li {
    width: 15px;
    height: 15px;
  }
  .view-product
    .modal
    .modal-dialog
    .modal-content
    .lenses-glass
    .table
    tbody
    tr
    th {
    font-size: 13px;
  }
  .lenses-glass {
    table thead {
      display: none;
    }
    tbody {
      tr {
        td {
          cursor: pointer;
        }
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px)

@media (min-width: 400px) and(max-width: 575.98px) {
  .view-product .add-to-cart .review {
    text-align: center;
    margin: 25px 0;
  }
  .view-product .modal .modal-dialog {
    max-width: 100%;
  }
  .modal {
    padding-right: 0 !important;
  }
  .view-product .modal .modal-dialog .modal-content .lenses-glass {
    padding: 1rem 4px;
  }
  .view-product
    .modal
    .modal-dialog
    .modal-content
    .lenses-glass
    .table
    tbody
    tr
    td
    ul
    li {
    width: 20px;
    height: 20px;
  }
}
// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {
  .view-product .modal .modal-dialog {
    max-width: 100%;
  }
  .modal {
    padding: 0 17px !important;
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px) {
  .view-product .modal .modal-dialog {
    max-width: 70%;
  }
  .modal {
    padding: 0 !important;
  }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {
  .view-product .modal .modal-dialog {
    max-width: 70%;
  }
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
}
