@import "../helpers/variables";

// Store Section
.store {
  background-color: #fff;
  text-align: center;
  padding-top: $navbarHeight;

  h2 {
    font-size: 3em;
    font-family: $DMFont;
    color: $backcolor;
    padding-top: 20px;
  }
  .divider {
    border: 1px solid $backcolor;
    width: 15%;
    margin: 15px auto;
    position: relative;
    i {
      position: absolute;
      top: -11px;
      width: 44px;
      color: $backcolor;
      background-color: #fff;
      font-size: 22px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .categories {
    margin: 50px 0;

    .cat {
      display: block;

      > li {
        display: inline-block;
        text-transform: uppercase;
        background: transparent;
        border: 2px solid $backcolor;
        color: $backcolor;
        font-size: 14px;
        margin: 0 15px 10px 0;
        letter-spacing: 2px;
        font-weight: 700;
        cursor: pointer;
        padding: 7px 16px;
        transition: 0.3s;

        &:hover {
          background-color: $backcolor;
          color: #fff;
        }
      }

      .active {
        background-color: $backcolor;
        color: #fff;
        // border: 1px solid $backcolor;
        border: 1px solid rgba(0, 0, 0, 0.1);
        box-shadow: 0 11px 15px rgba(0, 0, 0, 0.1);
      }
    }
    .menu {
      position: relative;
      &:hover .submenu {
        transition: 0.3s ease-in-out;
        display: block;
      }

      .submenu {
        position: absolute;
        top: 38px;
        border: 1px solid #25bad0;
        background-color: #fff;
        left: -1px;
        display: none;
        transition: 0.3s ease-in-out;
        z-index: 999;

        li {
          padding: 10px 0;
          width: 180px;
          height: 50px;
          text-align: center;
          color: $backcolor;
          &:hover {
            background-color: $backcolor;
            color: #fff;
          }
          img {
            width: 40%;
            padding-left: 17px;
            height: 40px;
            float: left;
            top: -5px;
            position: relative;
          }
          p {
            width: 58%;
            float: right;
            text-align: left;
            padding-left: 15px;
            line-height: 24px;
          }
        }
        .active {
          background-color: $backcolor;
          color: #fff;
          // border: 1px solid $backcolor;
          border: 1px solid rgba(0, 0, 0, 0.1);
          box-shadow: 0 11px 15px rgba(0, 0, 0, 0.1);
        }
      }
    }
  }

  .products {
    margin-top: 100px;

    .product {
      position: relative;
      padding: 0 15px 15px;
      border: 1px solid transparent;
      transition: all 150ms linear;
      background-color: #fff;

      &:hover {
        border: 1px solid rgba(0, 0, 0, 0.1);
        box-shadow: 0 11px 15px rgba(0, 0, 0, 0.1);
      }
      .product-image {
        margin: 20px auto 0;
        text-align: center;
        overflow: hidden;
      }
      .product-option {
        display: block;
        margin: 0;
        $carousel-breakpoints: (
          sm: 576px - 30px,
          md: 768px - 30px,
          lg: 992px - 30px,
          xl: 1200px - 30px
        );

        .c-carousel {
          align-items: center;
          display: flex;
          justify-content: space-between;
          width: 56%;
          margin: auto;
          .c-carousel__control {
            position: relative;
            &:first-of-type {
              left: -10px;
            }
            &:last-of-type {
              right: -10px;
            }
          }
          .c-carousel__item {
            display: inline-block;
            width: 33%;
            text-align: center;
            padding: 0;
            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              border: 1px solid rgba(0, 0, 0, 0.2);
              padding: 2px;
              background: #fff;
              cursor: pointer;
              transition: 0.3s;
            }
            img.active {
              border: 1px solid rgba(0, 0, 0, 0.8);
            }
          }

          &--disable-prev {
            .c-carousel__control--prev {
              opacity: 0.5;
            }
          }

          &--disable-next {
            .c-carousel__control--next {
              opacity: 0.5;
            }
          }

          @media screen and (min-width: map-get($carousel-breakpoints, "sm")) {
            &--small {
              width: 50%;
            }
          }

          &__control {
            flex: 1 1 40px;
            text-align: center;
            z-index: 2;
          }

          &__track {
            display: flex;
            flex: 1 1 100%;
            flex-wrap: nowrap;
            margin-left: -9px;
            margin-right: -15px;
            overflow: hidden;
            z-index: 1;
          }

          &__item {
            flex: 1 0 auto;
            padding: 0 15px;
            transition: all 0.5s ease-out;
            width: 30%;
          }
        }
      }

      .color-description {
        .color-name {
          font-size: 18px;
          padding: 15px 0;
          color: rgba(0, 0, 0, 0.5);
          font-family: $brandFont;
        }
      }
      .product-description {
        margin: 0 auto 10px;
        display: block;
        font-size: 25px;
        font-family: $DMFont;

        .name {
          text-align: left;
          a {
            text-decoration: none;
            color: #000;
          }
        }
        .price {
          text-align: right;
        }
      }
      .product-sell {
        a {
          display: inline-block;
          width: 95%;
          background: #fff;
          color: #000;
          font-size: 20px;
          border: 1px solid #000;
          font-family: $DMFont;
          transition: 0.4s;
          &:hover {
            background: #053740;
            color: #fff;
          }
        }
      }
    }
    .moreproduct {
      display: none;
    }
  }
  .load {
    padding-top: 20px;
    a {
      padding: 6px 50px;
      border: 1px solid #053740;
      font-size: 1.3em;
      color: #fff;
      border-color: $lightcolor;
      background-color: $lightcolor;
      &:hover {
        background-color: $backcolor;
        box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.5);
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .store {
    .divider {
      width: 30%;
    }
    .categories {
      .cat {
        display: block;
        padding: 0 22px;
      }
      .menu .submenu {
        width: 65%;

        li {
          width: 100%;
        }
      }
    }
  }
}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {
  .store .categories .menu .submenu {
    width: 100%;
    li {
      width: 100%;
    }
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px) {
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {
  body .store .products .product .product-option .c-carousel {
    width: 70%;
  }
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
}
