@import "../helpers/variables";

// Arrival Section
.arrival {
  padding-top: $navbarHeight;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.5);
  h2 {
    font-size: 3em;
    font-family: $DMFont;
    color: $backcolor;
    padding-top: 20px;
  }
  .divider {
    border: 1px solid $backcolor;
    width: 15%;
    margin: 15px auto;
    position: relative;
    i {
      position: absolute;
      top: -11px;
      width: 44px;
      color: $backcolor;
      background-color: #fff;
      font-size: 22px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .row {
    padding: 40px 0;
    .arrival-content {
      small {
        color: $lightcolor;
        text-transform: capitalize;
        font-size: 19px;
        padding-top: 10px;
        display: inline-block;
      }
      p {
        font-size: 30px;
        font-weight: bold;
        font-family: $DMFont;
      }
      a {
        padding: 7px 50px;
        border: 1px solid #053740;
        font-size: 1.3em;
        color: #fff;
        border-color: $lightcolor;
        background-color: $lightcolor;
        &:hover {
          background-color: $backcolor;
          box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.5);
        }
        &:focus,
        &:active {
          background-color: $backcolor;
        }
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .arrival .row .arrival-content {
    small {
      font-size: 19px;
    }
    p {
      font-size: 22px;
    }
    a {
      padding: 6px 30px;
    }
  }
  .arrival .divider {
    width: 30%;
  }
}
