@import "../helpers/variables";

// Features Section
.features {
  background-color: $backcolor;
  color: #fff;
  padding-top: $navbarHeight;
  h2 {
    font-size: 3em;
    font-family: $DMFont;
    padding-top: 20px;
  }
  .divider {
    border: 1px solid #fff;
    width: 15%;
    margin: 15px auto;
    position: relative;
    i {
      position: absolute;
      top: -11px;
      width: 44px;
      background-color: $backcolor;
      font-size: 22px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .feature {
    padding-top: $navbarHeight;

    .feat {
      box-shadow: 1px 2px 19px #000;

      padding-top: $navbarHeight;
      padding-bottom: $navbarHeight;

      i {
        font-size: 4em;
      }
      .feature-name {
        padding-top: 20px;
        font-size: 1.8em;
        text-transform: capitalize;
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .features .feature .feat {
    box-shadow: 1px 19px 36px #000;
  }
  .features .divider {
    width: 30%;
  }
}
