@import "../helpers/variables";

// Contact Section
.contact {
  text-align: center;
  background-color: $backcolor;
  padding-top: $navbarHeight;
  .fa-concierge-bell {
    margin-top: 25px;
    color: #fff;

    &:hover {
      color: $lightcolor;
      animation: shake 0.5s infinite;
    }
  }
  h2 {
    font-family: $DMFont;
    font-size: 3em;
    color: #fff;
  }
  .divider {
    border: 1px solid #fff;
    width: 15%;
    margin: 15px auto;
    position: relative;
    i {
      position: absolute;
      top: -11px;
      width: 44px;
      background-color: $backcolor;
      color: #fff;
      font-size: 22px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .lead {
    color: #bbb;
    font-size: 16px;
    margin-top: 20px;
  }

  form {
    margin-top: 40px;

    .form-group {
      width: 60%;
      margin: auto;
      position: relative;

      .form-control {
        background-color: rgba(0, 0, 0, 0.4);
        border: none;
        color: #fff;
        margin-bottom: 30px;

        &:hover {
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.75),
            0 0 8px rgba(102, 175, 233, 0.75);
        }
        &:focus {
          box-shadow: 0px 0px 2px 1px $lightcolor;
        }
        &:focus ~ label {
          top: -27px;
          left: 0;
          color: $lightcolor;
          font-weight: bold;
        }
      }
      label {
        position: absolute;
        top: 7px;
        left: 11px;
        color: #fff;
        pointer-events: none;
        transition: 0.5s;
      }
    }

    button {
      display: block;
      width: 60%;
      margin: auto;
      margin-top: 25px;
      background-color: $backcolor;
      border-color: $lightcolor;
      color: #fff;

      &:hover {
        background-color: $lightcolor;
        color: $backcolor;
        box-shadow: 0 1px 9px rgba(102, 175, 233, 0.75);
      }
      &:focus,
      &:active {
        background-color: $lightcolor;
        border-color: $backcolor;
        color: $backcolor;
      }
    }
  }

  .social {
    padding: 25px 0;
    font-size: 20px;

    a {
      margin-right: 30px;
      color: #fff;

      &:hover {
        color: $lightcolor;
      }
    }
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }

  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }

  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }

  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }

  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }

  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .contact {
    .divider {
      width: 30%;
    }
    form {
      .form-group {
        width: 85%;
      }
      button {
        width: 85%;
      }
    }
  }
}
// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {
  .contact form {
    .form-group {
      width: 85%;
    }
    button {
      width: 85%;
    }
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px) {
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {
  body .store .products .product .product-option .c-carousel {
    width: 70%;
  }
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
}
