@import "../helpers/variables";

// Home Section
.home {
  padding-top: $navbarHeight;
  height: 100vh;
  position: relative;
  overflow: hidden;
  .wrapper {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    background-image: url("../images/bg3-blur.jpg");
    background-size: 100% 100%;
    background-attachment: fixed;
    cursor: none;
    #cursor {
      position: absolute;
      width: 200px;
      height: 200px;
      border-radius: 50%;
      background-image: url("../images/bg3.jpg");
      background-size: 100% 100%;
      background-attachment: fixed;
      z-index: 2;
    }
    h1 {
      color: #fff;
      font-size: 6em;
      z-index: 9999;
      top: 50%;
      text-align: center;
      position: relative;
      transform: translate(0%, -50%);
      font-family: $DMFont;
    }
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .home .wrapper h1 {
    font-size: 4em;
  }
  #cursor {
    display: none;
  }
}

